import React, { Fragment, Component } from 'react';
import * as tambons from '../../../../jsonData/tambon';
import * as province from '../../../../jsonData/province';
import * as amphoes from '../../../../jsonData/amphoe';
import { TextValidator } from 'react-material-ui-form-validator';

import TambonSelect from './TambonSelection';

class AddressSelectors extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedTambon: '',
      selectedAmphoe: '',
      selectedProvince: '',
      searchInput: '',
      tambons: tambons.th.tambons,
      amphoes: amphoes.th.amphoes,
      provinces: province.th.changwats,
      selected: false,
      anchorEl: '',
      anchorElWidth: '',
    };
  }

  componentDidMount() {
    this.setState({
      anchorEl: document.getElementById('companyTambon'),
      anchorElWidth: document.getElementById('companyTambon').offsetWidth,
    });
  }

  handleSeachInput = event => {
    this.setState({
      selected: false,
      searchInput: event.target.value,
      anchorEl: event.currentTarget,
      anchorElWidth: event.currentTarget.offsetWidth,
    });
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  updateSelectedTambon = event => {
    this.setState({
      selectedTambon: this.state.tambons.find(
        tambon => tambon.name === event.target.innerText.trim()
      ),
    });
  };

  updateSelectedAmphoe = () => {
    this.setState(state => ({
      selectedAmphoe: state.amphoes.find(
        amphoe => amphoe.pid === state.selectedTambon.amphoe_pid
      ).name,
    }));
  };

  updateSelectedProvince = () => {
    this.setState(state => ({
      selectedProvince: state.provinces.find(
        province => province.pid === state.selectedTambon.changwat_pid
      ).name,
    }));
  };

  handleSelect = event => {
    this.updateSelectedTambon(event);
    this.updateSelectedAmphoe();
    this.updateSelectedProvince();
    this.setState({
      selected: true,
      searchInput: event.target.innerText.trim(),
    });
  };

  render() {
    const {
      searchInput,
      selected,
      selectedAmphoe,
      tambons,
      selectedProvince,
      anchorEl,
      anchorElWidth,
    } = this.state;
    const { handleSelect, handleSeachInput, handleChange } = this;

    const filteredTambons = tambons.filter(tambon =>
      tambon.name.includes(this.state.searchInput)
    );

    return (
      <Fragment>
        <TextValidator
          id="companyTambon"
          name="companyTambon"
          fullWidth
          style={{ margin: 8 }}
          label="ตำบล / แขวง"
          placeholder="กรอกชื่อตำบล / แขวง เพื่อค้นหา"
          helperText="ไม่มีให้เว้นว่าง"
          type="search"
          value={searchInput}
          onChange={handleSeachInput}
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          margin="dense"
        />
        <TambonSelect
          anchorElWidth={anchorElWidth}
          anchorEl={anchorEl}
          searchInput={searchInput}
          selected={selected}
          handleSelect={handleSelect}
          filteredTambons={filteredTambons}
        />
        <TextValidator
          id="subProvince"
          name="subProvince"
          onChange={handleChange('selectedAmphoe')}
          value={selectedAmphoe}
          helperText="กรุณาเลือกตำบล/แขวงก่อน | ไม่มีให้เว้นว่าง"
          label="อำเภอ / เขต"
          style={{ margin: 8 }}
          fullWidth
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          margin="dense"
        />
        <TextValidator
          id="province"
          name="province"
          onChange={handleChange('selectedProvince')}
          value={selectedProvince}
          helperText="กรุณาเลือกตำบล/แขวงก่อน | ไม่มีให้เว้นว่าง"
          label="จังหวัด"
          style={{ margin: 8 }}
          fullWidth
          validators={['maxStringLength:35']}
          errorMessages={['ความยาวสูงสุด 35 ตัวอักษร']}
          variant="outlined"
          margin="dense"
        />
      </Fragment>
    );
  }
}

export default AddressSelectors;
