import React, { Component } from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';
import {
  InputAdornment,
  IconButton,
  Tooltip,
  Typography,
} from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import CalculatorImg from '../../../../images/calculator.png';

class CIFValueBaht extends Component {
  constructor(props) {
    super(props);
    this.state = {
      cifValueBaht: '',
      hasError: false,
      helperText: 'ราคา CIF เงินบาท',
    };
  }

  handleChange = event => {
    this.setState({
      [event.target.name]: event.target.value,
    });
  };

  multiplyFloat = (first, second) => {
    const result = parseFloat(first) * parseFloat(second);
    const roundedResult = Math.round(Math.round(result * 1000) / 10) / 100;
    return roundedResult.toString();
  };

  getCIFValueBaht = () => {
    const exchangeRate = document.getElementById('exchangeRate').value;
    const cifValueForeign = document.getElementById('cifValueForeign')
      .value;

    if (exchangeRate !== '' && cifValueForeign !== '') {
      this.setState({
        cifValueBaht: this.multiplyFloat(exchangeRate, cifValueForeign),
        hasError: false,
        helperText: 'ราคา CIF เงินบาท',
      });
    } else {
      this.setState({
        helperText: 'กรุณากรอกช่อง Exchange Rate และ CIF Value Foreign',
        hasError: true,
        cifValueBaht: '',
      });
    }
  };

  render() {
    const { handleChange } = this;
    const { cifValueBaht, hasError, helperText } = this.state;
    return (
      <TextValidator
        required
        error={hasError}
        name="cifValueBaht"
        id="cifValueBaht"
        onChange={handleChange}
        value={cifValueBaht}
        label="CIF Value Baht"
        helperText={helperText}
        placeholder="0000.00"
        style={{ margin: 8 }}
        fullWidth
        validators={['required', 'isFloat', 'isCorrectCIFValue']}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'ต้องเป็นตัวเลขและจุดทศนิยมเท่านั้น',
          'กรอกเป็นตัวเลขสูงสุด 16 หลัก รวมจุดทศนิยมสูงสุด 2 ตำแหน่ง',
        ]}
        margin="dense"
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="คำนวณ" placement="left">
                <IconButton onClick={this.getCIFValueBaht}>
                  <img
                    alt="calculateBtn"
                    width="16.67"
                    height="16.67"
                    src={CalculatorImg}
                  />
                </IconButton>
              </Tooltip>
              <AdditionalInfoDialog
                titleTH="ราคา CIF เงินบาท"
                titleEN="CIF Value Baht"
                format="n..16,2"
                maxWidth="xs"
              >
                <Typography>
                  มีค่าเท่ากับ Exchange Rate คูณ CIF Value Foreign
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default CIFValueBaht;
