import React, { Component, Fragment } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { ListItem, ListItemText } from '@material-ui/core';
import ThreePropsMenu from './searchSelectorComponents/ThreePropsMenu';
import SearchSelectWrapper from './searchSelectorComponents/SearchSelectWrapper';

/* eslint-disable-next-line no-extend-native */
String.prototype.has = function(value) {
  return this.toLowerCase().includes(value);
};

class SearchSelector extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchInput: '',
      selected: false,
      validators: ['required'],
      errorMessages: ['จำเป็นต้องกรอก'],
      anchorEl: null,
      anchorElWidth: '',
    };
    this.handleBlur = this.handleBlur.bind(this);
  }

  componentDidMount() {
    const { required } = this.props;
    this.interval = null;
    this.setState({
      validators: required ? ['required'] : [],
      errorMessages: required ? ['จำเป็นต้องกรอก'] : [],
    });
  }

  componentWillUnmount() {
    this.setState({
      infoData: [],
    });
    clearTimeout(this.interval);
  }

  handleBlur(event) {
    event.preventDefault();
    this.interval = setTimeout(() => {
      this.setState(state => ({
        searchInput: state.selected ? state.searchInput : '',
      }));
    }, 200);
  }

  handleSeachInput = event => {
    event.preventDefault();
    this.setState({
      searchInput: event.target.value,
      selected: false,
      anchorEl: event.currentTarget,
      anchorElWidth: event.currentTarget.offsetWidth,
    });
  };

  handleSelect = event => {
    const { maxDigits, required, validators, errorMessages } = this.props;
    event.preventDefault();
    this.setState({
      selected: true,
      searchInput: event.currentTarget.innerText
        .trim()
        .slice(0, maxDigits),
      validators: required ? validators : [],
      errorMessages: required ? errorMessages : [],
    });
  };

  render() {
    const { handleSeachInput, handleSelect, handleBlur } = this;
    const {
      id,
      label,
      helperText,
      codeFirst,
      placeholder,
      firstDataProp,
      secondDataProp,
      thirdDataProp,
      maxDigits,
      required,
      infoData,
    } = this.props;
    const {
      searchInput,
      validators,
      errorMessages,
      selected,
      anchorEl,
      anchorElWidth,
    } = this.state;

    const lowerCasedInput = searchInput.toLowerCase();

    let filteredCodes = [];

    if (codeFirst) {
      filteredCodes =
        lowerCasedInput.length < maxDigits + 1
          ? infoData.filter(datum =>
              datum[firstDataProp].has(lowerCasedInput)
            )
          : infoData.filter(datum =>
              datum[secondDataProp].has(lowerCasedInput)
            );
    } else {
      filteredCodes = infoData.filter(datum =>
        thirdDataProp
          ? datum[secondDataProp].has(lowerCasedInput) ||
            datum[thirdDataProp].has(lowerCasedInput) ||
            datum[firstDataProp].has(lowerCasedInput)
          : datum[firstDataProp].has(lowerCasedInput) ||
            datum[secondDataProp].has(lowerCasedInput)
      );
    }

    return (
      <Fragment>
        <TextValidator
          id={id}
          name={id}
          fullWidth
          required={required}
          style={{ margin: 8 }}
          label={label}
          helperText={helperText}
          placeholder={placeholder}
          type="search"
          onBlur={handleBlur}
          value={searchInput}
          validators={validators}
          errorMessages={errorMessages}
          onChange={handleSeachInput}
          variant="outlined"
          margin="dense"
        />
        {selected || searchInput === '' ? (
          ''
        ) : (
          <SearchSelectWrapper
            anchorEl={anchorEl}
            anchorElWidth={anchorElWidth}
          >
            {filteredCodes.slice(0, 20).map((datum, i) => (
              <ListItem
                button
                key={i}
                onClick={handleSelect}
                value={datum[firstDataProp]}
              >
                {thirdDataProp ? (
                  <ThreePropsMenu
                    first={datum[firstDataProp]}
                    second={datum[secondDataProp]}
                    third={datum[thirdDataProp]}
                  />
                ) : (
                  <ListItemText
                    primary={datum[firstDataProp]}
                    secondary={datum[secondDataProp]}
                  />
                )}
              </ListItem>
            ))}
          </SearchSelectWrapper>
        )}
      </Fragment>
    );
  }
}

export default SearchSelector;
